// 登录相关的请求

import request from '@/utils/request.js'

export const orderadressApi = () => {
  return request.get("/address/list");
}

export const ordercheckoutApi = (mode, obj) => {
  return request.get("/checkout/order", {
    params: {
      mode,
      delivery: 10,
      couponId: 0,
      isUsePoints: 0,
      ...obj
    }
  });
}
export const SubmitOrderApi = (mode, obj) => {
  return request.post('/checkout/submit', {
    mode,
    delivery: 10, // 物流方式  配送方式 (10快递配送 20门店自提)
    couponId: 0, // 优惠券 id
    payType: 10, // 余额支付
    isUsePoints: 0, // 是否使用积分
    ...obj
  })
}

// 订单列表
export const orderListApi = (dataType, page) => {
  return request.get('/order/list', {
    params:{
      dataType,
      page,
    }
  })

}